@import 'vars';
html {
    width: 100%;
    font-size: unquote("min(100vw, 1360px)");
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

body {
    font-family: $font1;
    font-weight: 500;
    color: $black;
    font-size: 1.17647%;
    background-color: $blockBg;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    width: 100%;
    max-width: 1340px;
    margin: 0 auto;
    padding: 0 2.45em;
}

a {
    &:hover {
        color: $primary;
    }
}

@media screen and (max-width: 576px){
    body{
        font-size: 4.45%;
    }

    .container{
        padding: 0 .875em;
    }
}
