.footer{
  padding: 3.125em 0 2.5em;
  &__inner{
    padding: 2.5em 0;
    border-top: 1px solid $lightGray;
    display: flex;
    justify-content: space-between;
  }
  &__right{
    display: flex;
    flex-direction: column;
  }
  &__logo{
    display: block;
    width: 14.57em;
    margin-bottom: 1.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    margin-top: .75em;
    display: grid;
    grid-template-columns: 100%;
    gap: .25em;
    &-title{
      color: rgba(128, 120, 122, 0.6);
    }
  }
  &__links{
    &-wrap{
      padding-top: 3.75em;
    }
    &-items{
      margin-top: 1.25em;
      display: grid;
      grid-template-columns: 100%;
      gap: .875em;
    }
  }
  &__pay{
    display: flex;
    align-items: center;
    margin-top: 1.5em;
    &-item{
      &:not(:last-child){
        margin-right: 1.5em;
      }
      img{
        max-width: 100%;
      }
    }
  }
  &__socials{
    padding-top: 2.125em;
    display: flex;
    align-items: center;
    margin-top: auto;
  }
  &__social{
    width: 2.5em;
    height: 2.5em;
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(7px);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    &:hover{
      background: rgba(255, 255, 255, 1);
    }
    img{
      width: 1.125em;
    }
    &:not(:last-child){
      margin-right: 1.25em;
    }
  }
  &__text{
    color: rgba(128, 120, 122, 0.6);
  }
}

.call{
  position: fixed;
  right: 1.875em;
  bottom: 1.875em;
  z-index: 5;
  &._active{
    .call{
      &__btn{
        background-image: url(../img/close.svg);
        background-size: 1em auto;
      }
      &__dropdown{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__dropdown{
    position: absolute;
    right: 0;
    bottom: 100%;
    margin-bottom: .625em;
    padding: 1.25em;
    background: rgba(255, 255, 255, 0.9);
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2);
    border-radius: .375em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  &__group{
    display: flex;
    padding: 1.125em 0;
    border-bottom: 1px solid $lightGray;
    &-title{
      white-space: nowrap;
    }
    &-desc{
      color: rgba(128, 120, 122, 0.6);
    }
    &-info{
      padding: .125em 0;
    }
    &:first-child{
      padding-top: 0;
    }
    &:last-child{
      border: none;
      padding-bottom: 0;
    }
  }
  &__icon{
    width: 1.375em;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      width: 100%;
    }
  }
  &__btn{
    width: 4em;
    height: 4em;
    background-color: $primary;
    background-image: url(../img/phone.svg);
    background-size: 1.625em auto;
    background-position: center;
    background-repeat: no-repeat;
    border: 2px solid #FFFFFF;
    box-shadow: 0px .25em 1.5em rgba(65, 65, 65, 0.3);
    border-radius: 50%;
    font-size: 1em;
    &:hover{
      box-shadow: 0px 0.0625em .25em rgba(65, 65, 65, 0.8);
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 1.825em 0 2.5em;
    &__inner{
      display: block;
      padding: 1.5em 0;
    }
    &__logo{
      width: 11.425em;
    }
    &__info{
      gap: .125em;
    }
    &__links{
      &-wrap{
        display: none;
      }
    }
    &__btn{
      display: none;
    }
    &__social{
      background-color: $lightGray;
    }
  }

  .call{
    display: none;
  }
}
