.fg {
  position: relative;
  margin: 0;
  font-size: 1.125em;
  line-height: 156%;
  input {
    border: 1px solid $lightGray;
    border-radius: .445em;
    padding: 0 1.112em;
    height: 3.445em;
    width: 100%;
    font-size: 1em;
    line-height: inherit;
    color: $black;
    background: #FFFFFF;
    &::placeholder {
      color: rgba($black,.6);
    }
    &:hover,
    &:focus{
      border-color: $primary;
    }
    &:focus,
    &.input-active {
      &+label {
        font-size: .778em;
        line-height: 150%;
        top: .536em;
        left: 1.4282em;
        transform: translateY(0%);
      }
    }
  }
  label {
    transition: all .1s;
    pointer-events: none;
    position: absolute;
    left: 1.112em;
    font-size: 1em;
    line-height: inherit;
    top: 50%;
    transform: translateY(-50%);
    color: rgba($black,.6);
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}
