@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.sprite-black{
  fill: $black;
}

.sprite-primary{
  fill: $primary;
}

.icon18{
  width: 1.126em;
  height: 1.126em;
}

.icon16{
  width: 1em;
  height: 1em;
}

.icon12{
  width: 0.751em;
  height: 0.751em;
}

.swiper{
  &-button{
    width: 3.75em;
    height: 3.75em;
    background: rgba(255, 255, 255, 0.6);
    box-shadow: 6px 14px 24px rgba(128, 120, 122, 0.2);
    backdrop-filter: blur(7px);
    border-radius: 50%;
    &:hover{
      box-shadow: 0px 1px 4px rgba(128, 120, 122, 0.4);
    }
    img{
      width: 1.5em;
      display: block;
    }
  }
  &-pagination{
    display: flex;
    align-items: center;
    justify-content: center;
    position: static;
    margin-top: 1.5em;
    &-bullet{
      width: .5em;
      height: .5em;
      background-color: $lightGray;
      opacity: 1;
      margin: 0 .3125em !important;
      &-active{
        background-color: $primary;
      }
    }
  }
}

.m-badge{
  position: absolute;
  top: 0;
  left: 0;
  backdrop-filter: blur(7px);
  border-radius: 0 0 .75em 0;
  background-color: $primary;
  color: #fff;
  padding: .19em .5em;
}

.author{
  display: flex;
  align-items: center;
  &__img{
    margin-right: 1.25em;
    flex-shrink: 0;
    width: 3.125em;
    height: 3.125em;
    overflow: hidden;
    border: .0625em solid #fff;
    border-radius: 50%;
    filter: drop-shadow(0px 1px 11px rgba(0, 0, 0, 0.19));
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}


@media screen and (max-width: 576px){
  .author{
    &__img{
      width: 2.25em;
      height: 2.25em;
      margin-right: .625em;
    }
  }
}
