.main{
  padding: 3.75em 0 2em;
  &__inner{
    padding-top: 1.375em;
    position: relative;
  }
  &__title{
    width: 30.5em;
    margin: 0 auto;
  }
  &__img{
    margin-top: .25em;
    height: 33.23em;
    display: flex;
    justify-content: center;
    align-items: center;
    img{
      height: 100%;
      max-width: 100%;
      width: auto;
    }
  }
  &__info{
    position: absolute;
    top: -5.125em;
    left: 6.88em;
    &-text{
      position: absolute;
      left: 100%;
      bottom: 11.5em;
      width: 100%;
      max-width: 20em;
      transform: translateX(-1.5em);
    }
  }
  &__decor{
    //pointer-events: none;
    width: 48.19em;
  }
  &__nav{
    margin-top: .3125em;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__btn{
    display: inline-flex;
  }
  &__slide2{
    @media screen and (min-width: 577px){
      .main{
        &__title{
          text-align: center;
        }
        &__info{
          position: static;
          margin-top: .3125em;
          &-text{
            position: static;
            text-align: center;
            max-width: 100%;
            transform: unset;
          }
        }
        &__img{
          height: 32em;
        }
        &__decor{
          display: none;
        }
      }
    }
  }
  .swiper{
    &-container{
      position: relative;
    }
    &-button{
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
  }
}

.mob-nav{
  padding-bottom: .75em;
  @media screen and (min-width: 577px){
    display: none;
  }
  .container{
    padding: 0 1.5em;
  }
  &__inner{
    display: grid;
    grid-template-columns: repeat(4,25%);
    background: #FFFFFF;
    box-shadow: 0px 4px 20px rgba(119, 110, 112, 0.1);
    border-radius: .75em;
  }
  &__link{
    padding: .625em 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    &-title{
      font-size: .625em;
      line-height: 140%;
    }
  }
  &__icon{
    margin-bottom: .375em;
    width: auto;
    height: 1.5em;
  }
}

.advantages{
  padding: 2.5em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc((100% / 3 - 2em)));
    gap: 3em;
  }
}

.collection{
  padding: 2.5em 0;
  overflow: hidden;
  &__slider{
    margin-top: 2.5em;
    position: relative;
    .swiper{
      &-container{
        position: relative;
        &:before,
        &:after{
          content: '';
          position: absolute;
          height: 120%;
          width: 10000px;
          background-color: $blockBg;
          top: -10%;
          z-index: 2;
        }
        &:before{
          right: 100%;
        }
        &:after{
          left: 100%;
        }
      }
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        padding-right: 1em;
        width: 18.5em;
      }
      &-button{
        box-shadow: 6px 14px 24px rgba(128, 120, 122, 0.2);
        &-prev{
          left: 0;
          transform: translateX(-50%);
        }
        &-next{
          right: 0;
          transform: translateX(50%);
        }
      }
    }
  }
}

.how-recover{
  padding: 4.75em 0 1.5em;
  overflow: hidden;
  &__slider{
    margin-top: 2.5em;
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          display: grid;
          grid-template-columns: repeat(3,calc(100% / 3 - 2em));
          gap: 3em;
          transform: unset !important;
        }
        &-slide{
          width: 100% !important;
          &:nth-child(1){
            grid-column: 1/3;
            .doctor-card2{
              grid-template-columns: 36.97% auto;
              &__play{
                left: 50%;
                transform: translateX(-50%);
              }
              &__content {
                width: calc(100% + 1em);
                margin-left: -1em;
              }
              &__name{
                gap: 0.5em;
                .display3{
                  font-size: 2.375em;
                  line-height: 133%;
                }
                .text16{
                  font-size: 1.375em;
                  line-height: 127%;
                }
              }
              &__feedback,
              &__experience{
                font-size: 1em;
              }
              &__feedback{
                margin-right: 1.25em;
              }
              &__prof{
                margin-top: 1.25em;
                &-title{
                  font-size: .875em;
                }
                &-items{
                  margin-top: .5em;
                  column-count: 2;
                  column-gap: 1.25em;
                }
                &-item{
                  font-size: 1em;
                  &:not(:last-child){
                    margin-bottom: .25em;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
    align-items: center;
  }
}

.info{
  padding: 2.5em 0 6.25em;
  &__inner{
    background: radial-gradient(62.23% 670.3% at 91.8% 13.33%, #9E9396 0%, #80787A 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
    box-shadow: 0px 1.5em 3.125em rgba(119, 110, 112, 0.5);
    border-radius: .75em;
    padding: 2.5em 22em 2.5em 2.5em;
    color: $lightGray;
    position: relative;
    overflow: hidden;
  }
  &__img{
    width: 21.13em;
    position: absolute;
    top: 0;
    right: 0;
  }
  &__desc{
    margin-top: 1.43em;
  }
  &__items{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__item{
    display: flex;
    &:before{
      content: '';
      display: block;
      width: 1.5em;
      height: 1.5em;
      flex-shrink: 0;
      margin-right: .625em;
      background-image: url(../img/check.svg);
      background-size: 100% auto;
      background-repeat: no-repeat;
    }
    &-content{
      display: grid;
      grid-template-columns: 100%;
      gap: 0.25em;
    }
    &-desc{
      color: rgba(231, 225, 217, 0.6);
    }
  }
  &__nav{
    margin-top: 1.875em;
    display: flex;
    align-items: center;
  }
}

.info2{
  padding: 3.125em 0;
}

.rating,
.history,
.reward,
.media,
.equip,
.blog{
  background-color: #fff;
  overflow: hidden;
  &__slider {
    margin-top: 2.5em;
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          display: grid;
          transform: unset !important;
        }
        &-slide{
          &._desktop-hide{
            display: none;
          }
        }
      }
    }
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
    align-items: center;
  }
}

.history{
  padding: 6.25em 0 3.125em;
  &__desc{
    margin-top: 1.25em;
  }
  &__slider{
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          grid-template-columns: repeat(3,calc((100% / 3) - (2em / 3)));
          gap: 1em;
        }
        &-slide{
          width: 100% !important;
        }
      }
    }
  }
}

.rating{
  padding: 3.125em 0;
  &__slider {
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          grid-template-columns: repeat(6,calc((100% / 6) - (5em / 6)));
          gap: 1em;
        }
      }
    }
  }
}

.reward{
  padding: 3.125em 0 6.25em;
  &__title{
    text-align: center;
  }
  &__desc{
    margin-top: 1.25em;
    text-align: center;
  }
  &__nav{
    justify-content: center;
  }
  .swiper{
    @media screen and (min-width: 577px){
      &-wrapper{
        grid-template-columns: repeat(5,calc(20% - (4em / 5)));
        gap: 1em;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
}

.video{
  display: block;
  margin-bottom: 6.25em;
  position: relative;
  img{
    width: 100%;
    display: block;
    border-radius: .75em;
    filter: drop-shadow(0px .25em 1.25em rgba(119, 110, 112, 0.2));
  }
  &__play{
    width: 5em;
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    img{
      width: 100%;
    }
  }
}

.media{
  background-color: transparent;
  padding: 6.25em 0 3.125em;
  &__desc{
    margin-top: 1.25em;
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 1.125em;
    }
    &-top{
      display: flex;
      align-items: center;
      margin-bottom: .625em;
    }
    &-icon{
      width: 2.125em;
      flex-shrink: 0;
      margin-right: .625em;
      img{
        filter: drop-shadow(0px 1px 11px rgba(0, 0, 0, 0.19));
        width: 100%;
      }
    }
  }
  &__group2{
    margin-top: 1.125em;
    &:not(:last-child){
      margin-bottom: 1.125em;
    }
    &-img{
      width: 5.625em;
      margin-bottom: .75em;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__doc{
    img{
      width: 100%;
      display: block;
      filter: drop-shadow(0px .25em 1.25em rgba(119, 110, 112, 0.1));
      border-radius: .75em;
    }
  }
  .swiper{
    @media screen and (min-width: 577px){
      &-wrapper{
        display: flex;
      }
      &-slide{
        width: 18.94em;
        flex-shrink: 0;
        &:not(:last-child){
          margin-right: 2.5em;
        }
        &--small{
          width: 15.57em;
        }
      }
    }
  }
}

.equip{
  background-color: transparent;
  padding: 3.125em 0;
  &__desc{
    margin-top: 1.25em;
  }
  .swiper{
    @media screen and (min-width: 577px){
      &-wrapper{
        grid-template-columns: repeat(4,calc(25% - (9em / 4)));
        gap: 3em;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
}

.blog{
  padding: 3.125em 0;
  background-color: transparent;
  .swiper{
    @media screen and (min-width: 577px){
      &-wrapper{
        grid-template-columns: repeat(3,calc((100% / 3) - 1.655em));
        justify-content: space-between;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: 1.5em 0;
    &__inner{
      padding: 0;
      display: flex;
      flex-direction: column;
    }
    &__title{
      width: 100%;
      text-align: center;
    }
    &__info{
      position: static;
      margin-top: .625em;
      &-text{
        position: static;
        transform: unset;
        max-width: unset;
        text-align: center;
      }
    }
    &__decor{
      display: none;
    }
    &__img{
      height: 16.25em;
      order: 2;
      margin-top: 1em;
    }
    &__nav{
      margin-top: 1.25em;
    }
    .swiper{
      &-button{
        display: none;
      }
    }
  }

  .advantages{
    padding: 1.5em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: .75em;
    }
  }

  .collection{
    padding: 1.5em 0;
    &__slider{
      margin-top: 1.5em;
      .swiper{
        &-container{
          &:before,
          &:after{
            display: none;
          }
        }
        &-slide{
          padding-right: .75em;
          width: 17em;
        }
        &-button{
          display: none;
        }
      }
    }
  }

  .how-recover,
  .history,
  .rating,
  .reward,
  .media,
  .equip,
  .blog{
    &__slider {
      margin-top: 1.5em;
      .swiper{
        &-wrapper{
          align-items: stretch;
        }
        &-slide{
          height: auto;
        }
      }
    }
    &__nav{
      display: none;
    }
  }

  .how-recover{
    padding: 3em 0 .75em;
    .swiper{
      &-slide{
        width: 18.23em;
        padding-right: .75em;
      }
    }
  }

  .info{
    padding: 3.75em 0;
    &__inner{
      padding: 1.5em;
    }
    &__img{
      width: 7.5em;
      top: -.75em;
      right: -2em;
    }
    &__title{
      padding-right: 2.5em;
    }
    &__desc{
      padding-right: 4em;
      margin-top: .43em;
    }
    &__items{
      margin-top: .75em;
      gap: 1.125em;
    }
    &__item{
      &:before{
        width: 1em;
        height: 1em;
        margin-right: .5em;
      }
    }
    &__nav{
      margin-top: .875em;
    }
  }

  .info2{
    padding: 1.825em 0;
    .info__img{
      right: 0;
      width: 6.25em;
      top: 0;
    }
  }

  .history{
    padding: 3.75em 0 1.825em;
    &__desc{
      margin-top: .625em;
    }
    .swiper{
      &-slide{
        width: 19.5em;
        padding-right: .75em;
      }
    }
    &__info{
      height: 100%;
      background: #FFFFFF;
      border: 1px solid $lightGray;
      border-radius: .75em;
      padding: 1.5em;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      &-link{
        margin-top: 1em;
      }
      &-desc{
        margin-top: .33em;
      }
    }
  }

  .rating{
    padding: 1.825em 0;
    .swiper{
      &-slide{
        width: 9.8em;
        padding-right: .75em;
      }
    }
  }

  .reward{
    padding: 1.825em 0 3.75em;
    &__title{
      text-align: left;
    }
    &__desc{
      text-align: left;
      margin-top: .625em;
    }
    .swiper{
      &-slide{
        width: 14.5em;
        padding-right: .75em;
      }
    }
    .history__info{
      padding: 1.125em;
    }
  }

  .media{
    padding: 3.75em 0 1.825em;
    .container{
      display: flex;
      flex-direction: column;
    }
    .video{
      order: 2;
      margin: 3.75em 0 0;
    }
    &__group2{
      &-img{
        margin-bottom: .25em;
      }
    }
    .history{
      &__info{
        padding: 0.75em;
      }
      &__subtitle{
        padding: 0 .25em;
      }
    }
    .swiper{
      &-slide{
        width: 16.11em;
        padding-right: 3em;
      }
    }
  }

  .equip{
    padding: 1.825em 0;
    &__desc{
      margin-top: .625em;
    }
    .swiper{
      &-slide{
        width: 12em;
        padding-right: .75em;
      }
    }
  }

  .blog{
    padding: 1.825em 0;
    .swiper{
      &-slide{
        width: 19.5em;
        padding-right: .75em;
      }
    }
  }
}
