.menu{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background-color: $lightGray;
  transform: translateY(-100%);
  transition: all .3s;
  padding: 6.7em 0 0;
  overflow-y: auto;
  &__scroll{
    height: 100%;
    padding-bottom: 5.82em;
    overflow-y: auto;
  }
  &__grid{
    display: grid;
    grid-template-columns: 52.71% 37.413%;
    justify-content: space-between;
  }
  &__services{
    &-links{
      display: grid;
      grid-template-columns: 100%;
      gap: 1.875em;
      margin-top: 1.875em;
    }
    &-link{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &:hover{
        color: $primary;
      }
    }
  }
  &__chapters{
    &-links{
      display: grid;
      grid-template-columns: 100%;
      gap: 1.25em;
      margin-top: 1.25em;
    }
    &-link{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &:hover{
        color: $primary;
      }
      svg{
        width: .876em;
        height: 1em;
      }
    }
  }
  &__consult{
    margin-top: 2.5em;
    padding: 2.5em;
    background: radial-gradient(62.23% 670.3% at 91.8% 13.33%, #9E9396 0%, #80787A 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
    box-shadow: 0px 24px 50px rgba(119, 110, 112, 0.5);
    border-radius: .75em;
    color: $lightGray;
    font-size: 1em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-desc{
      margin-top: 1.43em;
    }
    &-btn{
      flex-shrink: 0;
      height: 3.402em;
      margin-left: 2em;
    }
    &-img{
      display: none;
    }
  }
  &__mob-links{
    display: none;
  }
}

.submenu{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: inherit;
  padding: 6.7em 0 0;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  z-index: 11;
  &__scroll{
    height: 100%;
    padding-bottom: 5.82em;
    overflow-y: auto;
  }
  &._show{
    opacity: 1;
    pointer-events: unset;
  }
  &__top{
    display: flex;
    align-items: center;
    padding-bottom: 1.25em;
    background: $lightGray;
    position: sticky;
    top: 0;
  }
  &__back{
    margin-right: 2.86em;
  }
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: 66.345% 31.31%;
    justify-content: space-between;
    &:not(:last-child){
      margin-bottom: 3em;
    }
  }
  &__subtitle{
    margin-bottom: .90909em;
  }
  &__services{
    padding-left: 1em;
    &-links{
      display: grid;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &-link{
      line-height: 150%;
    }
    &-price{
      margin-top: 2.8575em;
    }
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 3em;
    }
  }
  &__types,
  &__brands,
  &__doctors{
    &-grid{
      display: grid;
      grid-template-columns: repeat(4,1fr);
      gap: 1em;
    }
  }
  &__brands{
    &-grid{
      grid-template-columns: repeat(6,1fr);
    }
  }
  &__doctors{
    &-grid{
      grid-template-columns: repeat(3,1fr);
    }
  }
  &__questions{
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
  }
}

.type-card,
.brand-card{
  background: $lightBg;
  border-radius: .75em;
  padding: .75em 1.5em .75em;
  text-align: center;
  transition: all .3s;
  box-shadow: 0px 4px 20px rgba(119, 110, 112, 0.1);
  &:hover{
    box-shadow: 0px 1px 4px rgba(119, 110, 112, 0.4);
  }
  &__img{
    height: 2.51em;
    margin-bottom: .75em;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      height: 100%;
      width: auto;
    }
  }
}

.type-card{
  min-height: 7.76em;
}

.brand-card{
  padding-top: .25em;
  &__img{
    height: 3.126em;
    margin-bottom: .25em;
  }
}

.doctor-card{
  background: $lightBg;
  box-shadow: 0px 4px 20px rgba(119, 110, 112, 0.1);
  border-radius: .75em;
  padding: .75em .75em 0 0;
  display: grid;
  grid-template-columns: 46.03% auto;
  overflow: hidden;
  &__img{
    &-wrap{
      padding-bottom: 127.97%;
      position: relative;
    }
    img{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
      display: block;
    }
  }
  &__name{
    display: flex;
    flex-direction: column;
  }
  &__feedback{
    margin-top: .8334em;
  }
  &__experience{
    margin-top: .34em;
  }
}

.question-card{
  display: flex;
  align-items: center;
  padding: .5em .9em;
  background: $lightBg;
  border-radius: .5em;
  img{
    flex-shrink: 0;
    width: 1.25em;
    margin-right: .65em;
  }
}


@media screen and (max-width: 576px){
  .menu{
    padding-top: 4.4em;
    &__scroll{
      padding-bottom: 2em;
      &>.container{
        display: flex;
        flex-direction: column;
      }
    }
    &__grid{
      order: 2;
    }
    &__mob{
      &-links{
        display: flex;
        align-items: center;
        justify-content: center;
      }
      &-link{
        display: flex;
        align-items: center;
        font-size: .875em;
        img{
          width: 1.57em;
          display: block;
          margin-right: .72em;
        }
        &:not(:last-child){
          margin-right: 1.43em;
        }
      }
    }
    &__consult{
      margin-top: 1.65em;
      padding: 1.5em;
      display: block;
      position: relative;
      overflow: hidden;
      &-info{
        max-width: 12em;
      }
      &-title{
        font-size: 1.125em;
      }
      &-desc{
        margin-top: .5em;
      }
      &-btn{
        margin: .6em 0 0 0;
        font-size: 0.75em;
        height: 2.83em;
        padding: 0 1em;
        border-radius: .67em;
      }
      &-img{
        display: block;
        position: absolute;
        top: 0;
        right: -2.19em;
        width: 8.93em;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__title{
      display: none;
    }
    &__services{
      margin-top: 1.5em;
      &-links{
        margin-top: 0;
        gap: 1.5em;
      }
      &-link{
        svg{
          margin-left: 1.3em;
        }
      }
    }
    &__chapters{
      padding-top: 2.5em;
      border-top: 1px solid $gray;
      &-links{
        margin-top: 0;
        gap: 1.25em;
      }
      &-link{
        svg{
          width: 1em;
          margin-left: 1em;
        }
      }
    }
  }

  .submenu{
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    padding: 0;
    &__scroll{
      padding-bottom: 2.5em;
      &>.container{
        padding-bottom: 4em;
        position: relative;
      }
    }
    &__top{
      justify-content: space-between;
      width: calc(100% + 1.75em);
      margin-left: -.875em;
      padding: 0 .875em;
      height: 3.9em;
      margin-bottom: 1.65em;
      &:after{
        content: '';
        display: block;
        width: 1.25em;
      }
    }
    &__back{
      background-color: transparent !important;
      padding: 0;
      margin: 0;
      height: unset;
      span{
        display: none;
      }
      svg{
        width: 1.45em;
        height: 1.45em;
        margin: 0 !important;
        fill: $black;
      }
    }
    &__title{
      font-size: 1.125em;
      line-height: 122%;
      width: calc(100% - 2.5em);
      padding: 0 .5em;
      text-align: center;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__right{
      &._order1{
        order: -1;
      }
    }
    &__group{
      &:not(:last-child){
        margin-bottom: 2.5em;
      }
    }
    &__types{
      &-grid{
        grid-template-columns: repeat(2,calc(50% - .5em));
        justify-content: space-between;
      }
    }
    &__brands,
    &__doctors{
      &-grid{
        display: flex;
        align-items: stretch;
        gap: unset;
        overflow-x: auto;
        width: calc(100% + 1.75em);
        margin-left: -.875em;
        padding: 0 .875em;
        &::-webkit-scrollbar {
          width: 0;
          height: 0;
        }
      }
    }
    &__services{
      &-price{
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        margin: 0;
        padding: 1em;
      }
    }
    &__questions{
      gap: .65em;
    }
  }

  .type-card{
    padding: .6em;
    min-height: unset;
    &__img{
      margin-bottom: .125em;
    }
  }

  .brand-card{
    padding: .25em .25em .6em;
    flex-shrink: 0;
    width: 5.5em;
    min-height: unset;
    height: auto;
    &:not(:last-child){
      margin-right: .25em;
    }
    &__img{
      height: 2.51em;
      margin-bottom: .125em;
    }
  }

  .doctor-card{
    width: 16.9em;
    flex-shrink: 0;
    grid-template-columns: 42.3% auto;
    &:not(:last-child){
      margin-right: 1.25em;
    }
    &__info{
      display: flex;
      align-items: center;
      margin-top: .5em;
    }
    &__feedback,
    &__experience{
      font-size: 0.75em;
      margin-top: 0;
    }
    &__feedback{
      margin: 0 .8em 0 0;
    }
  }
}


