@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .3s;
    padding: 0;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-big{
    font-size: 1.25em;
    padding: 0 1.2em;
    height: 3.102em;
    border-radius: .6em;
}

.m-btn-middle{
    font-size: 1em;
    padding: 0 1.25em;
    height: 3.125em;
    border-radius: .5em;
    svg{
        &:first-child{
            margin-right: .5em;
        }
    }
}

.m-btn-small{
    font-size: .875em;
    padding: 0 1em;
    height: 3em;
    border-radius: .572em;
}

.m-btn-light{
    background-color: $light;
    color: $black;
    &:hover{
        background-color: #F4F0E9;
        color: rgba($black,.8);
    }
}

.m-btn-dark{
    background-color: $black;
    color: $lightGray;
    svg{
        fill:$lightGray;
    }
    &:hover{
        background-color: #60585A;
        color: $lightBg;
        svg{
            fill: $lightBg;
        }
    }
}

.text-btn{
    display: flex;
    align-items: center;
}

.text-link{
    color: $linkBlue;
}

.text-btn-middle{
    font-size: 0.875em;
    line-height: 129%;
    color: $black;
    img{
        width: 1.575em;
        margin-right: .715em;
    }
    &:hover{
        color: $gray;
    }
}



@media screen and (max-width: 576px){
    .m-btn-small{
        font-size: .75em;
        height: 2.75em;
        border-radius: .67em;
    }

    .m-btn-middle{
        font-size: .875em;
        padding: 0 1em;
        height: 2.9em;
        svg{
            width: 1.285em;
            height: 1.285em;
        }
    }

    .m-btn-middle-mob{
        font-size: .875em;
        padding: 0 .875em;
        height: 2.86em;
        border-radius: .57em;
    }

    .m-btn-small-mob{
        font-size: .75em;
        padding: 0 1em;
        height: 2.8em;
        border-radius: .67em;
    }
}
