.m-card{
  transition: all .3s;
  box-shadow: 0px .25em 1.5em rgba(119, 110, 112, 0.1);
  &:hover{
    box-shadow: 0px 0.0625em .25em rgba(119, 110, 112, 0.4);
  }
}

.advantage-card{
  padding: 2.5em;
  background: #FFFFFF;
  border-radius: .75em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.25em;
  }
  &__icon{
    flex-shrink: 0;
    margin-left: .375em;
    width: 2.125em;
    display: block;
  }
  &__desc{
    line-height: 150%;
    color: rgba(128, 120, 122, 0.6);
  }
}

.product-card{
  background: #FFFFFF;
  box-shadow: 0px 4px 20px rgba(119, 110, 112, 0.1);
  border-radius: .75em;
  overflow: hidden;
  &__top{
    padding: .5em .625em;
    width: calc(100% - 1em);
    background: $primary;
    backdrop-filter: blur(7px);
    border-radius: 0px 0px .75em 0px;
    color: #fff;
  }
  &__hint{
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__img{
    display: flex;
    justify-content: center;
    padding: .875em;
    height: 10.25em;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__bottom{
    padding: .875em 1.5em 1.5em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    width: 100%;
    margin-bottom: 1.25em;
  }
  &__btn{
    margin-right: auto;
    font-size: 1em;
    padding: 0 1.25em;
    height: 3.125em;
    border-radius: .5em;
  }
}

.doctor-card2{
  display: grid;
  grid-template-columns: 58.17% auto;
  background: #FFFFFF;
  border-radius: .75em;
  height: 100%;
  overflow: hidden;
  &__img{
    width: 100%;
    max-height: 95%;
    object-fit: cover;
    display: block;
    &-wrap{
      position: relative;
      height: 100%;
      display: flex;
      align-items: flex-end;
    }
  }
  &__play{
    width: 3.75em;
    position: absolute;
    z-index: 2;
    bottom: 2.5em;
    left: 4.25em;
    img{
      width: 100%;
      filter: drop-shadow(6px 14px 24px rgba(128, 120, 122, 0.2));
    }
  }
  &__content{
    width: calc(100% + 3.66em);
    margin-left: -3.66em;
    padding: 1.875em .875em 1.875em 0;
    position: relative;
    z-index: 3;
  }
  &__name{
    display: grid;
    grid-template-columns: 100%;
    gap: .25em;
    font-size: 1em;
  }
  &__info{
    display: flex;
    align-items: center;
    margin-top: .875em;
  }
  &__feedback{
    margin-right: 1em;
    color: $linkBlue;
  }
  &__prof{
    margin-top: .875em;
    &-title{
      margin-bottom: .0625em;
      color: rgba(128, 120, 122, 0.6);
    }
  }
}

.history-card{
  position: relative;
  display: grid;
  grid-template-columns: auto 48.4%;
  background: $lightGray;
  border-radius: .75em;
  overflow: hidden;
  padding-top: 1em;
  &__img{
    align-self: flex-end;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding: 2.25em 1em 1.5em 1.5em;
    display: flex;
    flex-direction: column;
  }
  &__name{
    margin-bottom: 2.125em;
    font-size: 1em;
  }
  &__nav{
    margin-top: auto;
    display: flex;
  }
}

.rating-card{
  padding: 1.5em .875em;
  background: $blockBg;
  border-radius: 12px;
  text-align: center;
  &__img{
    display: flex;
    justify-content: center;
    height: 2.125em;
    img{
      object-fit: cover;
      max-width: 100%;
      height: 100%;
      display: block;
    }
  }
  &__num{
    margin-top: .75em;
    text-align: center;
    color: $primary;
  }
  &__site{
    color: rgba(128, 120, 122, 0.6);
  }
}

.reward-card{
  text-align: center;
  &__img{
    height: 8.625em;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(../img/starts.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto 100%;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      width: 7.75em;
      height: .5em;
      top: 100%;
      left: 50%;
      transform: translate(-50%,.875em);
      background: radial-gradient(71.12% 25% at 50% 50%, #80787A 0%, rgba(128, 120, 122, 0) 100%);
      filter: blur(4.5px);
    }
    img{
      max-height: 100%;
      max-width: 100%;
      transform: translateY(.4em);
    }
  }
  &__title{
    margin-top: 2.25em;
    color: $primary;
  }
  &__info{
    margin-top: .375em;
    display: grid;
    grid-template-columns: 100%;
    gap: .25em;
  }
}

.equip-card{
  &__img{
    padding-bottom: 64.44%;
    position: relative;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: .75em;
    }
  }
  &__content{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: 100%;
    gap: .25em;
  }
  &__desc{
    color: rgba(128, 120, 122, 0.6);
  }
}

.blog-card{
  background: #fff;
  border-radius: .75em;
  overflow: hidden;
  height: 100%;
  &__top{
    padding-bottom: 59.64%;
    position: relative;
    background-size: cover;
    background-position: center;
  }
  &__info{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1.5em;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__badge{
    display: inline-flex;
    padding: .0625em .5em;
    background: $primary;
    backdrop-filter: blur(7px);
    border-radius: .375em;
    margin: 0 auto .625em 0;
  }
  &__title{
    width: 100%;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    &:hover{
      color: #fff;
      opacity: .8;
    }
  }
  &__content{
    padding: 1.5em;
  }
  &__props{
    display: flex;
    align-items: center;
    margin-top: .875em;
  }
  &__prop{
    display: flex;
    align-items: center;
    color: rgba(128, 120, 122, 0.6);
    &:not(:last-child){
      margin-right: 1.875em;
    }
    img{
      flex-shrink: 0;
      width: 1em;
      margin-right: .375em;
    }
  }
}

.blog-card2{
  padding: 1.5em;
  background-color: #fff;
  border-radius: .75em;
  &__img{
    margin: 1.25em 0;
    position: relative;
    padding-bottom: 62.7%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
      border-radius: .375em;
    }
  }
}


@media screen and (max-width: 576px){
  .advantage-card{
    padding: 1.5em;
    &__top{
      margin-bottom: .625em;
    }
  }

  .product-card{
    &__top{
      padding: .25em .5em;
      width: calc(100% - 3em);
    }
    &__title{
      margin-bottom: 1.75em;
    }
    &__btn{
      font-size: .75em;
      padding: 0 1em;
      height: 2.8em;
      border-radius: .67em;
    }
  }

  .doctor-card2{
    grid-template-columns: 51.8% auto;
    &__content{
      width: calc(100% + 1.67em);
      margin-left: -1.67em;
      display: flex;
      flex-direction: column;
    }
    &__img{
      height: 12em;
    }
    &__name{
      gap: .125em;
    }
    &__info{
      order: 2;
      flex-direction: column;
      align-items: unset;
      margin-top: 0.5em;
    }
    &__prof{
      margin-top: .125em;
      &-title{
        display: none;
      }
    }
    &__play{
      bottom: 1em;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  .history-card{
    grid-template-columns: auto 42%;
    padding: 0;
    &__content{
      padding: 2.125em 1.5em 1.5em;
    }
    &__img{
      height: 100%;
      display: flex;
      align-items: flex-end;
      img{
        width: 135%;
      }
    }
    &__name{
      margin-bottom: 2.5em;
    }
  }

  .rating-card{
    padding: .875em;
    &__img{
      height: 2.685em;
      align-items: center;
      img{
        width: 5em;
        height: auto;
        max-height: 100%;
      }
    }
    &__num{
      margin-top: .5em;
    }
  }

  .equip-card{
    &__img{
      padding-bottom: 69.5%;
    }
    &__content{
      margin-top: .625em;
      gap: 0;
    }
  }

  .blog-card{
    &__info{
      padding: .875em;
    }
    &__content{
      padding: 1.5em .875em;
    }
  }

  .blog-card2{
    &__img{
      margin: .875em 0;
      padding-bottom: 101.7%;
    }
  }
}
