.header{
  position: relative;
  z-index: 3;
  background: rgba(255, 255, 255, 0.9);
  box-shadow: 0px 4px 20px rgba(119, 110, 112, 0.1);
  &__top{
    display: flex;
    align-items: center;
    padding: 1.25em 0;
    border-bottom: .15em solid $lightGray;
  }
  &__logo{
    display: block;
    width: 11.251em;
    margin-right: 2.5em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone{
    display: flex;
    align-items: center;
    &-btn{
      font-size: 1em;
      margin-left: 1.25em;
      width: 2.627em;
      height: 2.126em;
      border: .15em solid rgba(231, 225, 217, 0.6);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: .5em;
      svg{
        fill: $black;
        width: 1.126em;
        height: 1.126em;
      }
    }
  }
  &__right{
    display: flex;
    align-items: center;
    margin-left: auto;
  }
  &__burger,
  &__search-btn{
    font-size: 1em;
    margin-left: 2.5em;
    width: 1.376em;
    height: 1.376em;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% auto;
  }
  &__burger{
    background-image: url(../img/burger.svg);
  }
  &__search-btn{
    background-image: url(../img/search.svg);
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.25em 0;
  }
  &__clinics{
    font-size: 1em;
    display: flex;
    position: relative;
    svg{
      margin: .25em 0.375em 0 0;
    }
    &-info{
      display: flex;
      flex-direction: column;
    }
    &-title{
      color: $linkBlue;
    }
    &._show{
      .clinic-dropdown{
        opacity: 1;
        pointer-events: unset;
        &__inner{
          transform: translateY(0);
        }
      }
    }
  }
  &._open-menu{
    .menu{
      transform: translateY(0%);
    }
    .header{
      &__logo,
      &__burger{
        position: relative;
        z-index: 11;
      }
      &__burger{
        background-image: url(../img/burger2.svg);
      }
    }
  }
  &._open-search{
    .header{
      &-search{
        transform: translateY(0%);
      }
      &__search-btn{
        z-index: 11;
        transform: translateX(3.876em);
        background-image: url(../img/burger2.svg);
      }
      &__burger{
        pointer-events: none;
        opacity: 0;
      }
      &__logo,
      &__burger{
        position: relative;
        z-index: 11;
      }
    }
  }
}

.lang{
  margin-right: 2.5em;
  flex-shrink: 0;
  font-size: 1em;
  position: relative;
  &__show{
    display: flex;
    align-items: center;
    svg{
      margin-left: .375em;
    }
  }
  &__item{
    font-size: .75em;
    &._selected{
      color: $gray;
    }
    ._mob{
      display: none;
    }
  }
  .dropdown{
    left: 50%;
    transform: translateX(-50%);
  }
  &._show{
    .dropdown{
      opacity: 1;
      pointer-events: unset;
      &__inner{
        transform: translateY(0);
      }
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-weight: 500;
    font-size: 1em;
    line-height: 112%;
    &:not(:last-child){
      margin-right: 2.5em;
    }
  }
}

.nav2{
  display: flex;
  align-items: center;
  &__link{
    margin-right: 2.143em;
  }
  &__sublink{
    line-height: 150%;
  }
  &__more{
    position: relative;
    &-show{
      display: flex;
      align-items: center;
      svg{
        width: 1.2em;
        height: 1.2em;
        margin-right: .45em;
      }
      &:hover{
        color: $primary;
        cursor: pointer;
        svg{
          fill: $primary;
        }
      }
    }
    .dropdown{
      left: unset;
      right: 0;
      font-size: 1.143em;
    }
    &._show{
      .dropdown{
        opacity: 1;
        pointer-events: unset;
        &__inner{
          transform: translateY(0);
        }
      }
    }
  }
}

.dropdown{
  position: absolute;
  top: 100%;
  left: 0;
  padding-top: .5em;
  font-size: 1em;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &__inner{
    transform: translateY(2em);
    padding: 1.875em;
    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0px 4px 54px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(7px);
    border-radius: .75em;
    transition: inherit;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
}

.clinic-dropdown{
  width: 20.2em;
  &__title{
    font-size: 1.125em;
    line-height: 122%;
    margin-bottom: .2225em;
    font-weight: 400;
  }
}

.nav2-dropdown{
  width: 20.2em;
}

.header-search{
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  padding: 6.7em 0 0;
  transition: all .3s;
  background-color: $lightGray;
  transform: translateY(-100%);
  &._founded{
    .header-search{
      &__scroll{
        overflow-y: auto;
      }
      &__clear,
      &__items{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__scroll{
    height: 100%;
    padding-bottom: 5.82em;
    overflow-y: hidden;
  }
  &__title{
    text-align: center;
  }
  &__field{
    margin: 2.225em 0;
    input{
      height: 4.34em;
      padding: 0 5em 0 3.4445em;
    }
    svg{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 1em;
      height: 1em;
      left: 1.78em;
    }
  }
  &__clear{
    position: absolute;
    right: 2.55em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.35em;
    opacity: 0;
    pointer-events: none;
    img{
      width: 100%;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
}

.search-card{
  background-color: $blockBg;
  border-radius: .5em;
  padding: 1.25em 2.5em;
  &__subtitle{
    margin-bottom: .84em;
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: 1.25em 0;
    box-shadow: none;
    &__top{
      padding: 0;
      border: none;
      justify-content: flex-end;
    }
    &__bottom{
      display: none;
    }
    &__right{
      margin: 0;
    }
    &__logo{
      width: 7.8em;
      margin-right: auto;
    }
    &__phone{
      &-link{
        display: none;
      }
      &-btn{
        padding: 0;
        border: none;
        svg{
          width: 100%;
          height: 100%;
        }
      }
    }
    &__phone-btn,
    &__search-btn,
    &__burger{
      width: 1.25em;
      height: 1.25em;
      margin-left: 1.5em;
    }
    &._open-menu{
      .header{
        &__logo{
          z-index: 1;
        }
      }
      .lang{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &._open-submenu{
      .lang{
        opacity: 0;
        pointer-events: none;
      }
    }
  }

  .nav{
    display: none;
  }

  .lang{
    position: absolute;
    top: 1em;
    left: 50%;
    transform: translateX(-50%);
    z-index: 11;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__show{
      display: none;
    }
    &__item{
      padding: .72em 0;
      font-size: .9em;
      position: relative;
      &:not(:last-child){
        margin-right: 1.42em;
      }
      &:before{
        content: '';
        position: absolute;
        border-radius: 2px 2px 0px 0px;
        width: 100%;
        height: 2px;
        background: $primary;
        bottom: 0;
        left: 0;
        opacity: 0;
        transition: all .3s;
      }
      &._selected{
        &:before{
          opacity: 1;
        }
      }
      ._desktop{
        display: none;
      }
      ._mob{
        display: block;
      }
    }
    .dropdown{
      position: static;
      opacity: 1;
      pointer-events: unset;
      width: unset;
      transform: unset;
      padding: 0;
      &__inner{
        padding: 0;
        box-shadow: none;
        transform: unset;
        background-color: transparent;
        display: flex;
        align-items: center;
        gap: unset;
        backdrop-filter: unset;
      }
    }
  }


}
